<template>
  <div style="height: 100vh">
    <el-container>
      <el-header>
       <Header></Header>
      </el-header>
      <el-container>
        <el-main>
          <!--
          prop model  validateresetFields 
          -->
          <!-- direction="vertical"-->

          <el-col :span="12">
            <div class="table">
              <div class="sontable">
                <h1></h1>
                <el-divider></el-divider>
                <el-form :model="param" :rules="rules" ref="login" label-width="80px">
                  <el-form-item label="" prop="username">
                    <el-input type="text" placeholder="" size="small"
                              prefix-icon="el-icon-user"
                              v-model="param.username"></el-input>
                  </el-form-item>
                  <el-form-item label="" prop="email">
                    <el-input type="text" placeholder="" size="small"
                              prefix-icon="el-icon-connection"
                              v-model="param.email"></el-input>
                  </el-form-item>
                  <el-form-item label="" prop="name">
                    <el-input type="text" placeholder="" size="small"
                              prefix-icon="el-icon-postcard"
                              v-model="param.name"></el-input>
                  </el-form-item>

                  <el-form-item label="" prop="sex">
                    <div>
                      <el-radio v-model="param.sex" label="1" size="large"></el-radio>
                      <el-radio v-model="param.sex" label="2" size="large"></el-radio>
                    </div>
                  </el-form-item>
                  <el-form-item label="" prop="password">
                    <el-input type="password" placeholder="6" size="small" show-password prefix-icon="el-icon-lock"
                              v-model="param.password"></el-input>
                  </el-form-item>
                  <el-form-item label="" prop="qrpassword">
                    <el-input type="password" placeholder="" size="small" show-password prefix-icon="el-icon-lock"
                              v-model="param.qrpassword"></el-input>
                  </el-form-item>
                  <el-form-item label="" prop="yzm">
                    <el-input type="text" placeholder="" size="small"
                              prefix-icon="el-icon-picture"
                              v-model="param.yzm"></el-input>
                  </el-form-item>
                  &nbsp;&nbsp;<el-button type="danger" @click="Submit('register')" style="margin-left: 10px"></el-button>
                  &nbsp;<el-button type="primary" @click="goHome()" style="margin-left: 10px"></el-button>
                </el-form>

              </div>
            </div>
          </el-col>
          <el-col :span="12">
            <div style="margin-left: 10px;margin-top: 100px">
              <el-card>
                <span></span>
                <p><br/>
                  <br/>
                   </p>
              </el-card>
            </div>
          </el-col>
        </el-main>
      </el-container>
      <el-foot>
        <Footer></Footer>
      </el-foot>
    </el-container>
  </div>
</template>

<script>
import Header from "@/components/Header";
import Footer from "@/components/Footer";
import ElementUI from 'element-ui';

export default {

name: "Register",
  components: {Header,Footer},
  data(){
    return {
      param:{
        username:"",
        email:"",
        name:"",
        sex:"",
        password:"",
        qrpassword:"",
        ymz:""
      },
      //
      rules:{
        username:[
          {required:true,message:"",trigger:"blur"}
        ],
        email:[
          {type:"email",message:"",trigger:"blur"},
          {required:true,message:"",trigger:"blur"}
        ],
        name:[
          {required:true,message:"",trigger:"blur"}
        ],
        password:[
          {required:true,message:"",trigger:"blur"},
          {min:6,max:30,message:"630",trigger:"blur"}
        ],
        qrpassword:[
          {required:true,message:"",trigger:"blur"},
          {}
        ],
        yzm:[
          {required:true,message:"",trigger:"blur"}
        ],
      }
    }

  },
  methods:{
    Submit(){

      let params = new URLSearchParams();
      params.append('ticket', this.param.username);
      params.append( 'userpass', this.param.password);
      params.append( 'email', this.param.email);
      // 12
      params.append( 'xb', this.param.sex);
      params.append('name', this.param.name);

      this.axios.post("register",
          params
      ).then((response) => {
        console.log(response.data)
        console.log(response.data['msg'])
        if( response.data['msg'] == "ok"){        //

          ElementUI.Message({
            message: '5',
            type: 'success'
          });

          setTimeout(
            //5
            this.$router.push("/login")         //
          , 5000);
        }else{          //
          //this.error_msg = response.data['error']
          ElementUI.Message({
            message: '',
            type: 'error'
          });
        }
      })
    },
    ///
    goHome(){
      this.$router.push("/");
    },
    //
    goRegister(){
      this.$router.push("/register");
    },
    //
    goForget(){
      this.$router.push("/");
    },
  }
}
</script>

<style lang="less" scoped>
/**/
.table{
  margin-top:20px;
  margin-left: 20px;
  width: 600px;
  font-size:14px;
  line-height:34px;
}
.sontable{
  margin-top:100px;
  margin-left:300px;
}
.el-card{
  width: 270px;
  height: 320px;
  margin-top:250px;
  margin-left:150px;
 span{
    font-weight: bolder;
 }
  p{
    margin-top:20px;
    font-size:14px;
    color:#424242;
    line-height:34px;
  }
}
.el-input{
  width:240px;
}
</style>