<template>
  <div>
    <el-tabs>
      <el-tab-pane label="" name="first">
        <el-table :data="tableData" style="width: 100%">
          <el-table-column prop="time" label="" width="180" />
          <el-table-column prop="title" label="" width="180" />
          <el-table-column prop="author" label="" width="180" />
          <el-table-column prop="body" label="" width="180"/>
          <el-table-column prop="isemail" label="" width="180" />
          <el-table-column prop="operation" label="" width="180">
            <template  #default="scope">
              <el-button type="primary" circle >
                <i class="el-icon-edit"></i>
              </el-button>
              <el-button type="danger" circle @click="DelAnnounce(scope.$index)">
                <i class="el-icon-delete"></i>
              </el-button>
              <el-button type="success" circle @click="LookAnnounce(scope.$index)">
                <i class="el-icon-view"></i>
              </el-button>
            </template>
          </el-table-column>
        </el-table>
      </el-tab-pane>

      <el-tab-pane label="" name="second">
        <el-form :model="param" :rules="rules" ref="announce" label-width="80px">
          <el-form-item label="" prop="time">
            <el-input type="text" placeholder="" size="small"
                      prefix-icon="el-icon-time"
                      v-model="param.time"></el-input>
          </el-form-item>
          <el-form-item label="" prop="title">
            <el-input type="text" placeholder="" size="small"
                      prefix-icon="el-icon-connection"
                      v-model="param.title"></el-input>
          </el-form-item>
          <el-form-item label="" prop="title">
            <el-input type="text" placeholder="" size="small"
                      prefix-icon="el-icon-connection"
                      v-model="param.author"></el-input>
          </el-form-item>
          <el-form-item label="" prop="content">
            <el-input placeholder="" size="small"
                      prefix-icon="el-icon-news"
                      :autosize="{ minRows: 9, maxRows: 15 }"
                      type="textarea"
                      v-model="param.body"></el-input>
          </el-form-item>

          <el-form-item prop="isEmail">
            <el-checkbox v-model="param.isemail" label="" size="large"></el-checkbox>
          </el-form-item>
          &nbsp;&nbsp;&nbsp;&nbsp;<el-button type="danger" @click="Submit"></el-button>
        </el-form>

      </el-tab-pane>
    </el-tabs>

  </div>
</template>

<script>
import ElementUI from "element-ui";

export default {
  //
  name: "AdminAnnounce",
  data(){
    return {
      tableData : [],
      param:{
        time:"",
        title:"",
        author:"",
        body:"",
        isemail:false,
      },
      rules: {
        time: [
          {required: true, message: "", trigger: "blur"}
        ],
        title: [
          {required: true, message: "", trigger: "blur"}
        ],
        content: [
          {required: true, message: "", trigger: "blur"}
        ],
      }
    }
  },
  mounted(){
    this.GetAnnounce()
  },
  methods:{
    GetAnnounce(){
      this.axios.post("showannounce"
      ).then((response) => {
        console.log(response.data)
        if (response.data['msg'] == "ok") {
          //this.tableData = response.data["announce"]
          this.tableData = []
          for( let i = 0 ; i < response.data["announce"].length ;  i++ ) {
            this.tableData.push({
              title: response.data["announce"][i]['title'],
              time: response.data["announce"][i]['time'],
              author: response.data["announce"][i]['author'],
              body: response.data["announce"][i]['body'].slice(0, 80) + "",      //100
              isemail: response.data["announce"][i]['isemail']
            })
          }
        }
      })
    },
    DelAnnounce(index){
      console.log(index+" "+this.tableData[index])
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        let params = new URLSearchParams();
        params.append('time',this.tableData[index]['time']);
        params.append('title',this.tableData[index]['title']);
        this.axios.post("deleteannounce",
            params
        ).then((response) => {
          console.log(response.data)
          if (response.data['msg'] == "ok") {
            this.$forceUpdate()         //
            this.tableData = response.data["announce"]
            this.GetAnnounce();
          }
        })
        this.$message({
          type: 'success',
          message: '!'
        });
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        });
      });
    },
    LookAnnounce(index){
      let time = this.tableData[index]['time']
      let title = this.tableData[index]['title']
      this.$router.push("/announcement?title="+title+"&time="+time);
    },
    Submit(){
      this.$confirm('', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        //axiosnull
        let params = new URLSearchParams();
        params.append('title', this.param.title);
        params.append('time', this.param.time);
        params.append('author', this.param.author);
        params.append('body', this.param.body);
        params.append('isemail', this.param.isemail);
        console.log(params)
        this.axios.post("addannounce",
            params
        ).then((response) => {
          console.log(response.data)
          console.log(response.data['msg'])
          if( response.data['msg'] == "ok"){
            ElementUI.Message({
              message: '',
              type: 'success'
            });
            this.GetCode();
          }else {
            ElementUI.Message({
              message: '',
              type: 'error'
            });
          }
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        });
      });

    }
  },
}
</script>

<style scoped>

</style>