<template>
  <div>

    <div class="block">

      <h1></h1>
      <br />
      <el-table :data="timeData" style="width:520px">
        <el-table-column prop="begin" label="" width="180"  />
        <el-table-column prop="end" label="" width="180" />
        <el-table-column prop="operation" label="" width="150">
          <template>
            <el-button type="danger" circle @click="deleteTime" >
              <i class="el-icon-close"></i>
            </el-button>
          </template>
        </el-table-column>
      </el-table>
      <br />
      <el-form :model="param"  ref="login" label-width="80px">
          <el-form-item label="" prop="begin">
            <el-date-picker
                v-model="param.begin"
                type="datetime"
                placeholder="Select date and time"
            >
            </el-date-picker>
          </el-form-item>

        &nbsp;
          <el-form-item label=" " prop="end">

          <el-date-picker
              v-model="param.end"
              type="datetime"
              placeholder="Select date and time"
          >
          </el-date-picker>
          </el-form-item>
        &nbsp;&nbsp;&nbsp;
        <el-button type="primary" @click="setTime"></el-button>
        &nbsp;
        <el-button type="primary"></el-button>
      </el-form>
      <el-divider></el-divider>

      <h1></h1>

      <el-table :data="tableData" style="width: 100%">
        <el-table-column prop="name" label="" width="180" />
        <el-table-column prop="permission" label="" width="180" />
        <el-table-column prop="note" label="" width="180"/>
        <el-table-column prop="operation" label="" width="180">
          <template  #default="scope">
            <el-button type="primary" circle >
              <i class="el-icon-edit"></i>
            </el-button>
            <el-button type="danger" icon="Delete" circle @click="deleteAdmin(scope.$index)">
              <i class="el-icon-delete"></i>
            </el-button>
          </template>
        </el-table-column>

      </el-table>
      <br />
      &nbsp;&nbsp;
      <div style="width: 100%">
        <el-button type="primary" style="margin-right: 10px" @click="addAdmin"></el-button>
      </div>

      <el-divider></el-divider>


      <h1></h1>
      <el-input
          disabled="true"
          rows="9"
          type="textarea"
          placeholder="Please input"
      />



      </div>

  </div>
</template>

<script>
export default {
  //
  name: "AdminJurisdict",
  data() {
    return {
      param:{
        begin:"",
        end:"",
      },
      timeData:[
        {
          begin:"",
          end:"",
        }
      ],
      time1: "",
      time2: "",
      tableData : [
        {
          name: 'admin',
          permission:'',
          note:'',

        },
        {
          name: 'abc',
          permission:'',
          note:'',
          operation:'',
        },
      ]

    }
  },
  mounted() {
    this.getTime()
  },
  methods: {
    //
    deleteTime(){
      this.axios.post("deleteopentime"
      ).then((response) => {
        console.log(response.data)
        if (response.data['msg'] == "ok") {
          this.tableData = [
            {
              begin:"",
              end:"",
            }
          ]
        }
      })
    },
    //
    getTime(){
      this.axios.post("getopentime"
      ).then((response) => {
        console.log(response.data)
        if (response.data['msg'] == "ok") {
          let data = [{
            begin:response.data["begin"],
            end:response.data["end"],
          }]
          this.timeData = data
        }
      })
    },
    //
    setTime(){

      this.$confirm(''+String(this.param.begin)+"~"+this.param.end, '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {

        let params = new URLSearchParams();
        params.append('begin',this.param.begin);
        params.append('end',this.param.end);

        this.axios.post("setopentime",
            params
        ).then((response) => {
          console.log(response.data)
          if (response.data['msg'] == "ok") {
            this.$forceUpdate()         //
            this.getTime()
          }
        })
        this.$message({
          type: 'success',
          message: '!'
        });
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        });
      });
    },

    deleteAdmin() {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this.$message({
          type: 'success',
          message: '!'
        });
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        });
      });
    },
    addAdmin(){
      this.$prompt('', '', {
        confirmButtonText: '',
        cancelButtonText: '',
      }).then(({ value }) => {
        this.$message({
          type: 'success',
          message: ': ' + value
        });
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        });
      });
    }
  }
}
</script>

<style scoped>

</style>