<template>
  <div>
    <el-form :model="param" :rules="rules" ref="login" label-width="80px">
      <el-form-item label="" prop="username">

        &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;<el-input type="text" placeholder="" size="small"
                  prefix-icon="el-icon-search"
                  v-model="param.find" style="width: 350px"></el-input>
        &nbsp; &nbsp;
        <el-button type="primary" >
          <i class="el-icon-search"></i>
          
        </el-button>
        <el-collapse accordion style="width: 450px;margin-left: 60px;white-space: pre-wrap;">
          <el-collapse-item title="   " name="1">
          <el-card style="width: 540px">
            <div>
              <span></span>&nbsp;&nbsp;&nbsp;
              <br/>&nbsp;
              <el-link type="primary" @click="toTag('')"></el-link>&nbsp;
              <el-link type="primary" @click="toTag('')"></el-link>&nbsp;
              <el-link type="primary" @click="toTag('')"></el-link>&nbsp;
              <el-link type="primary" @click="toTag('')"></el-link>&nbsp;
              <el-link type="primary" @click="toTag('')"></el-link>
            </div>
            <div>
              <span></span>&nbsp;&nbsp;&nbsp;
              <br/>&nbsp;
              <el-link type="primary" @click="toTag('')"></el-link>&nbsp;
              <el-link type="primary" @click="toTag('')"></el-link>&nbsp;
            </div>
            <div>
              <span></span>&nbsp;&nbsp;&nbsp;
              <br/>&nbsp;
              <el-link type="primary" @click="toTag('')"></el-link>&nbsp;
              <el-link type="primary" @click="toTag('')"></el-link>&nbsp;
            </div>
            <div>
              <span></span>&nbsp;&nbsp;&nbsp;
              <br/>&nbsp;
              <el-link type="primary" @click="toTag('')"></el-link>&nbsp;
              <el-link type="primary" @click="toTag('')"></el-link>&nbsp; |&nbsp;
              <el-link type="primary" @click="toTag('')"></el-link>&nbsp;
              <el-link type="primary" @click="toTag('')"></el-link>&nbsp;

            </div>

            <div>
              <span></span>&nbsp;&nbsp;&nbsp;
              <br/>&nbsp;
              <el-link type="primary" @click="toTag('')"></el-link>&nbsp;
              <el-link type="primary" @click="toTag('')"></el-link>&nbsp; |&nbsp;
              <el-link type="primary" @click="toTag('')"></el-link>&nbsp;

            </div>
          </el-card>
            <br>
            <el-tag
                v-for="tag in tags"
                :key="tag.name"
                class="mx-1"
                closable
                :type="tag.type"
            >
              {{ tag.name }}
            </el-tag>
          </el-collapse-item>
        </el-collapse>
        </el-form-item>






      <!--
          <el-tag
              v-for="tag in dynamicTags"
              :key="tag"
              class="mx-1"
              closable
              :disable-transitions="false"
              @close="handleClose(tag)"
          >
            {{ tag }}
          </el-tag>
          <el-input
              v-if="inputVisible"
              ref="InputRef"
              v-model="inputValue"
              class="ml-1 w-20"
              size="small"
              @keyup.enter="handleInputConfirm"
              @blur="handleInputConfirm"
          >
          </el-input>
        -->
    </el-form>
    <div style="margin-left: 65%">
      <el-tooltip
          effect="dark"
          content=""
          placement="top">
        <el-button type="primary" @click="AllpassSign">
          
        </el-button>
      </el-tooltip>
      <el-tooltip
          effect="dark"
          content=""
          placement="top">
        <el-button type="primary"  @click="AllrefuseSign"></el-button>
      </el-tooltip>
      <el-button type="primary"></el-button>
    </div>
    <el-table :data="tableData" style="width: 100%">
      <el-table-column prop="name" label="" width="70" />
      <el-table-column prop="sex" label="" width="50" />
      <el-table-column prop="sfzh" label="" width="100" />
      <el-table-column prop="ticket" label="" width="100" />
      <el-table-column prop="tele" label="" width="100" />
      <el-table-column prop="email" label="" width="100" />
      <el-table-column prop="zycc" label="" width="100" />
      <el-table-column prop="zymc" label="" width="100" />
      <el-table-column prop="kcdm" label="" width="100" />
      <el-table-column prop="file" label="" width="100" #default="scope" >
        <el-link v-for="(f,key) in segmenteFile(scope.row.file)" :key="key" target="_blank" :href="f.fsrc">{{f.fname}}</el-link>
      </el-table-column>
      <el-table-column prop="res" label="" width="100" />
      <el-table-column  label="" width="180" >
        <template #default="scope">
          <div v-if="tableData[scope.$index].res == '' ">
            <el-button type="primary" circle @click="PassSign(scope.$index)">
              <i class="el-icon-check"></i>
            </el-button>
            <el-button type="danger" circle @click="RefuseSign(scope.$index)">
              <i class="el-icon-close"></i>
            </el-button>
          </div>
          <div v-if="tableData[scope.$index].res == '' ">
            <el-button type="success" circle @click="BackSign(scope.$index)">
              <i class="el-icon-back"></i>
            </el-button>
          </div>
          <div v-if="tableData[scope.$index].res =='' ">
            <el-button type="success" circle @click="BackSign(scope.$index)">
              <i class="el-icon-back"></i>
            </el-button>
          </div>
        </template>



      </el-table-column>
    </el-table>
    <br />
    <template>
      <el-pagination background layout="prev, pager, next" :total="pagenum"
                     :hide-on-single-page="false"
                     page-size="10"
                     page-count="12">
      </el-pagination>
    </template>

  </div>
</template>

<script>

export default {
  //
  name: "AdminExamine",
  data(){
    return {
      //
      pagenum:"1",
      //
      page:1,
      //
      tags :[
        { name: 'Tag 1', type: '' },
      ],
      //
      tagchoose:{
        content:{
          which:"0",    //0

        }

      },
      param: {
        find: "",
      },

      tableData : []

    }
  },
  mounted() {
    this.$forceUpdate()         //
    this.calcPage();
    //
    this.showPage(this.page);
  },
  methods:{
    //
    calcPage(){
      this.axios.post("signupnum",
      ).then((response) => {
        console.log(response.data)
        if (response.data['msg'] == "ok") {
          this.pagenum = response.data['num']
        }
      })
    },
    //
    showPage(page){
      let params = new URLSearchParams();
      params.append("page",page)
      this.axios.post("examine",
          params
      ).then((response) => {
        console.log(response.data)
        if (response.data['msg'] == "ok") {
          this.tableData = response.data['examine']
        }
      })
    },
    //(file)
    segmenteFile(file){
      let array = file.split('|');
      console.log(array)
      console.log(array.length)
      let list = [];
      for(let i = 0 ; i < array.length ; i++ ){
        let names = array[i].split('_');
        list.push({'fname':names[1],'fsrc': this.$store.state.baseurl +array[i]})
      }
      console.log(list)
      return list;
    },
    //
    toTag(message){
      this.tags.push({ name: message, type: '' })
    },
    //
    PassSign(index){
      let params = new URLSearchParams();
      params.append("ticket",this.tableData[index]["ticket"])
      params.append("res","1")
      this.axios.post("auditstatus",
          params
      ).then((response) => {
        console.log(response.data)
        if (response.data['msg'] == "ok") {
          this.$forceUpdate()
          this.showPage(this.page)
          console.log(response.data)
        }
      })
    },
    //
    RefuseSign(index){
      let params = new URLSearchParams();
      params.append("ticket",this.tableData[index]["ticket"])
      params.append("res","-1")
      this.axios.post("auditstatus",
          params
      ).then((response) => {
        console.log(response.data)
        if (response.data['msg'] == "ok") {
          this.$forceUpdate()
          console.log(response.data)
          this.showPage(this.page)
        }
      })
    },
    //
    BackSign(index){
      let params = new URLSearchParams();
      params.append("ticket",this.tableData[index]["ticket"])
      params.append("res","0")
      this.axios.post("auditstatus",
          params
      ).then((response) => {
        console.log(response.data)
        if (response.data['msg'] == "ok") {
          this.$forceUpdate()
          console.log(response.data)
          this.showPage(this.page)
        }
      })
    },
    //
    AllpassSign(){
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this.$message({
          type: 'success',
          message: '!'
        });
        for(let i = 0 ; i < this.tableData.length ; i++ ){
          if( this.tableData[i]['res'] == "" ){
            this.PassSign(i);
          }
        }
        this.$forceUpdate()
        this.getInfo()

      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        });
      });
    },
    //
    AllrefuseSign(){
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this.$message({
          type: 'success',
          message: '!'
        });
        for(let i = 0 ; i < this.tableData.length ; i++ ){
          if( this.tableData[i]['res'] == "" ){
            this.RefuseSign(i);
          }
        }
        this.$forceUpdate()
        this.getInfo()
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        });
      });
    },
  }

}
</script>

