<template>
  <div style="height: 100vh">
    <el-container>
      <el-header>
        <Header></Header>
      </el-header>
      <el-container>
        <el-main>
          <!--
          prop model  validateresetFields 
          -->
          <!-- direction="vertical"-->

          <el-col :span="12">
            <div class="table">
              <div class="sontable">
                <h1>/</h1>
                <el-divider></el-divider>

                <div>
                  <el-form :model="param" :rules="rules" ref="login" label-width="80px">

                    <el-form-item label="" prop="sfz">
                      <el-input type="text" placeholder="" size="small"
                                prefix-icon="el-icon-user"
                                v-model="param.eyzm"></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="sfz">
                      <el-input type="text" placeholder="6" size="small"
                                prefix-icon="el-icon-user"
                                v-model="param.password"></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="sfz">
                      <el-input type="text" placeholder="" size="small"
                                prefix-icon="el-icon-user"
                                v-model="param.qrpassword"></el-input>
                    </el-form-item>
                    <el-button type="primary" @click="Submit">/</el-button>
                  </el-form>
                </div>


              </div>
            </div>
          </el-col>
          <el-col :span="12">
            <div style="margin-left: 10px;margin-top: 100px">
              <el-card width="300px">
                <span></span>
                <p>
                  <br/>2021
                  <br/>5. </p>
              </el-card>
            </div>
          </el-col>
        </el-main>
      </el-container>
      <el-foot>
        <Footer></Footer>
      </el-foot>
    </el-container>
  </div>
</template>

<script>

import Header from "@/components/Header";
import Footer from "@/components/Footer";
import ElementUI from "element-ui";

export default {
  name: "Forgetpass",
  components: { Header, Footer},
  data(){
    return {
      type:"",
      username:"",
      param:{
        eyzm:"",
        password:"",
        qrpassword:"",
      },
      //
      rules:{
        eyzm:[
          {required:true,message:"",trigger:"blur"}
        ],
        password:[
          {required:true,message:"",trigger:"blur"}
        ],
        qrpassword:[
          {required:true,message:"",trigger:"blur"}
        ],
      }
    }
  },
  methods:{
    Submit(){
      console.log(this.$store.state.username)
      //axiosnull
      let params = new URLSearchParams();
      params.append('username',this.$store.state.username);
      params.append('password', this.param.password);
      params.append('eyzm', this.param.eyzm);

      this.axios({
        method: 'post',
        url:"modifypass",
        withCredentials:true,
        data:params,
        dataType:'JSONP',  // Ajax,
      }).then((response) => {
        /*
        * {'msg':'ok'}
        * {'msg':'fail','error':''}
        */
        console.log(response.data)

        console.log(response.data['msg'])
        if( response.data['msg'] == "ok"){        //
          // user
          this.$store.commit('login', response.data)
          ElementUI.Message({
            message: '5',
            type: 'success'
          });
          setTimeout(
              //5
              this.$router.push("/login")         //
              , 5000);

        }else{      //
          ElementUI.Message({
            message: ''+response.data['error']+"",
            type: 'error'
          });
        }

      })
    }
  }
}
</script>

<style lang="less" scoped>

/**/
.table{
  margin-top:20px;
  margin-left: 20px;
  width: 600px;
  font-size:14px;
  line-height:34px;
}
.sontable{
  margin-top:100px;
  margin-left:300px;
}
.el-card{
  width: 270px;
  height: 320px;
  margin-top:100px;
  margin-left:150px;
span{
  font-weight: bolder;
}
p{
  margin-top:20px;
  font-size:14px;
  color:#424242;
  line-height:34px;
}
}
.el-input{
  width:240px;
}
</style>