<template>
  <el-container>
    <el-main>
      <!--
      prop model  validateresetFields 
      -->
      <!-- direction="vertical"-->

      <el-col :span="12">
        <div class="table">
          <div class="sontable">
            <h1></h1>
            <el-divider></el-divider>
            <el-form :model="form" :rules="rules" ref="login" label-width="80px">
              <el-form-item label="" prop="name">
                <el-input type="text" placeholder="" size="small"
                          prefix-icon="el-icon-postcard"
                          v-model="form.name"></el-input>
              </el-form-item>
              <el-form-item label="" prop="sex">
                <div>
                  <el-radio v-model="form.sex" label="1" size="large"></el-radio>
                  <el-radio v-model="form.sex" label="2" size="large"></el-radio>
                </div>
              </el-form-item>
              <el-form-item label="" prop="sfzh">
                <el-input type="password" placeholder=">X" size="small" show-password prefix-icon="el-icon-lock"
                          v-model="form.sfzh"></el-input>
              </el-form-item>

              <el-form-item label="" prop="ticket">
                <el-input type="text" placeholder="" size="small"
                          prefix-icon="el-icon-picture"
                          v-model="form.ticket"></el-input>
              </el-form-item>
              <el-form-item label="" prop="tele">
                <el-input type="text" placeholder="" size="small"
                          prefix-icon="el-icon-picture"
                          v-model="form.tele"></el-input>
              </el-form-item>
              <el-form-item label="" prop="email">
                <el-input type="text" placeholder="" size="small"
                          prefix-icon="el-icon-picture"
                          v-model="form.email"></el-input>
              </el-form-item>
              <el-form-item label="" prop="zycc">
                <el-select v-model="form.zycc" placeholder="Select" size="small" @change="Change">
                  <el-option
                      v-for="item in options"
                      :key="item.value"
                      :label="item.label"
                      :value="item.value"
                  ></el-option>
                </el-select>
              </el-form-item>

              <el-form-item label="" prop="zymc">
                <el-select v-model="form.zymc" placeholder="Select" size="small">
                  <el-option
                      v-for="item in options_zymc"
                      :key="item.value"
                      :label="item.label"
                      :value="item.value"
                  ></el-option>
                </el-select>
              </el-form-item>

              <el-form-item label="" prop="kcdm">
                <el-checkbox-group v-model="form.kcdm">
                  <el-checkbox  v-for="item in kcdm"
                                :label="item.name+'('+item.code+')'"
                                :key="item.code"
                  >
                  </el-checkbox>
                </el-checkbox-group>
              </el-form-item>
              <el-form-item label="">
              <el-upload
                  ref="upload"
                  list-type="text"
                  :on-preview="handlePictureCardPreview"
                  :before-upload="beforeUpload"
                  :on-remove="handleRemove"
                  :auto-upload="false"
                  :multiple="true"
                  :limit="3"
                  show-file-list="true"
                  :on-exceed="handleExceed"
                 
              >
                <el-button type="primary"></el-button>
                <!--<el-button type="primary"></el-button>-->
                <template #tip>
                  <div class="el-upload__tip">
                    JPG/PNG/PDF500KB
                  </div>
                </template>
              </el-upload>
              </el-form-item>
              &nbsp;&nbsp;&nbsp;&nbsp;<el-button type="danger" @click="Submit" disabled="step==2"></el-button>
              &nbsp;&nbsp;&nbsp;<el-button type="primary" ></el-button>

            </el-form>
          </div>
        </div>
      </el-col>
      <el-col :span="12">
        <div style="margin-left: 10px;margin-top: 100px">
          <el-card style="width: 250px">
            <span></span><br>
            <br>
            <br>
            <br>
            
          </el-card>
        </div>
      </el-col>
    </el-main>
  </el-container>
</template>

<script>

export default {
  name: "Signupinfo",
  data(){
    return {
      kcdm:[],
      param: "", // 
      form:{
        username:"",
        password:"",
        ymz:"",
        zycc:"",
        zymc:"",
        kcdm:[],
      },
      steps:"1",
      //
      rules:{
        username:[
          {required:true,message:"",trigger:"blur"}
        ],
        password:[
          {required:true,message:"",trigger:"blur"}
        ],
        yzm:[
          {required:true,message:"",trigger:"blur"}
        ],
      },
      options: [
        {
          value: '',
          label: '',
        },
        {
          value: '',
          label: '',
        },
      ],
      fileList: [],
      options_zymc: [],

    }

  },
  mounted(){      //
    this.GetCode();
    this.$forceUpdate()         //
  },
  methods: {
    Buttonstate(){
      let param = new URLSearchParams();
      param.append("username",this.$store.state.user.username )
      this.axios.post("signupres",
          param
      ).then((response) => {
        console.log(response.data)
        console.log(response.data['msg'])
        this.$forceUpdate()         //
        if( response.data['msg'] == "ok"){
          //  res01-12
          if( response.data['res'] == "2" ){         //
            this.steps = "1";
          }else if( response.data['res'] == "0"  ){  //
            this.steps = "2";
          }else if( response.data['res'] == "1" || response.data['res'] == "-1"  ){     //
            this.steps = "3";
          }
        }
      })
    },
    GetCode(){
      console.log("" )
      this.axios.post("showcode"
      ).then((response) => {
        console.log(response.data)
        if (response.data['msg'] == "ok") {
          this.kcdm = response.data["course"]
        }
      })
    },
    Change(){
      this.GetCode();
      this.form.zymc = ""
      if( this.form.zycc == "" ){
        this.options_zymc = [
          {
            value: '',
            label: '',
          },
          {
            value: '',
            label: '',
          },
        ]
      }else if( this.form.zycc == "" ) {
        this.options_zymc = [
        {
          value: '',
          label: '',
        },
        { value: '',
            label: '',
        },
      ]
      }
    },
    Submit() {
      this.$confirm('?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this.$refs.upload.submit();
        console.log(this.param)
        this.param.append('name',this.form.name);
        this.param.append('sex',this.form.sex);
        this.param.append('sfzh',this.form.sfzh);
        this.param.append('ticket',this.form.ticket);
        this.param.append('tele',this.form.tele);
        this.param.append('email',this.form.email);
        this.param.append('zycc',this.form.zycc);
        this.param.append('zymc',this.form.zymc);
        this.param.append('kcdm',this.form.kcdm);
        //    
        let configs = {
          headers: { "Content-Type": "multipart/form-data" }
        };

        this.axios.post("upload",
            this.param,configs
        ).then((response) => {
          console.log(response.data)
          if (response.data['msg'] == "ok") {
            this.$forceUpdate()         //
          }
        })
        this.$message({
          type: 'success',
          message: '!'
        });
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        });
      });
    },
    ///
    goHome() {
      this.$router.push("/index");
    },
    //
    goRegister() {
      this.$router.push("/register");
    },
    //
    goAdminLogin() {
      this.$router.push("/adminlogin");
    },
    //
    goForget() {
      this.$router.push("/forget");
    },
    handleRemove(file, fileList) {
      console.log(file, fileList);
    },
    handlePictureCardPreview(file) {
      this.dialogImageUrl = file.url;
      this.dialogVisible = true;
    },
    beforeUpload(file) {
      this.param = new FormData();
      this.fileList.push(file);
      let images = [...this.fileList];
      console.log("images", images);

      //this.param.append("file1", images);

      images.forEach((img) => {
        this.param.append("file", img); // 
      });
      return false;
    },
  }
}
</script>


<style lang="less" scoped>

/**/
.table{
  margin-top:10px;
  margin-left: 0px;
  width: 600px;
  font-size:14px;
  line-height:34px;
}
.sontable{
  margin-top:10px;
  margin-left:30px;
}
.el-card{
  width: 270px;
  height: 320px;
  margin-top:100px;
  margin-left:150px;
  span{
    font-weight: bolder;
  }
  p{
    margin-top:20px;
    font-size:14px;
    color:#424242;
    line-height:34px;
  }
}
.el-input{
  width:240px;
}
</style>