<template>
  <div style="width: 100%">
    <template>
      <el-steps :active="steps" style="width: 60%;margin-left: 10%">
        <el-step title="1" description=""></el-step>
        <el-step title="2" description=""></el-step>
        <el-step title="3" description=""></el-step>
      </el-steps>
      <div v-show="steps==1" style="margin-left: 30%;margin-top: 100px">

        <el-card style="width: 300px;">
          <h1 style="text-align: center"></h1>
          &nbsp;
         <el-button type="danger" @click='goSignUp'></el-button>
        </el-card>
      </div>
      <div v-show="steps==2" style="margin-left: 25%;margin-top: 100px">
        <template>
          <el-card style="width: 420px;">
          <el-descriptions title="" style="width: 380px;"
                           width="3px"
                           :column="1"
                           size="large"
                           label-align="center"
                           align="center"
                           border>
            <el-descriptions-item label="">{{infoData.name}}</el-descriptions-item>
            <el-descriptions-item label="">{{infoData.sex}}</el-descriptions-item>
            <el-descriptions-item label="">{{infoData.sfzh}}</el-descriptions-item>
            <el-descriptions-item label="">{{infoData.ticket}}</el-descriptions-item>
            <el-descriptions-item label="">{{infoData.tele}}</el-descriptions-item>
            <el-descriptions-item label="">{{infoData.zycc}}</el-descriptions-item>
            <el-descriptions-item label="">{{infoData.zymc}}</el-descriptions-item>
            <el-descriptions-item label="">{{infoData.kcdm}}</el-descriptions-item>
            <el-descriptions-item label="">
              <el-link v-for="(f,key) in segmenteFile(infoData.file)" :key="key" target="_blank" :href="f.fsrc">{{f.fname}}</el-link>
            </el-descriptions-item>
          </el-descriptions>
          <el-button type="danger" style="margin-left: 30%;margin-top: 20px" @click="goSignUp"></el-button>
          </el-card>
        </template>
      </div>

      <el-dialog :visible="dialogFormVisible" title="" :close-on-click-modal="false" :append-to-body="true">
        <el-form :model="form" :rules="rules" ref="login" label-width="80px">
          <el-form-item label="" prop="name">
            <el-input type="text" placeholder="" size="small"
                      prefix-icon="el-icon-postcard"
                      v-model="form.name"></el-input>
          </el-form-item>
          <el-form-item label="" prop="sex">
            <div>
              <el-radio v-model="form.sex" label="1" size="large"></el-radio>
              <el-radio v-model="form.sex" label="2" size="large"></el-radio>
            </div>
          </el-form-item>
          <el-form-item label="" prop="sfzh">
            <el-input type="password" placeholder=">X" size="small" show-password prefix-icon="el-icon-lock"
                      v-model="form.sfzh"></el-input>
          </el-form-item>

          <el-form-item label="" prop="ticket">
            <el-input type="text" placeholder="" size="small"
                      prefix-icon="el-icon-picture"
                      v-model="form.ticket"></el-input>
          </el-form-item>
          <el-form-item label="" prop="tele">
            <el-input type="text" placeholder="" size="small"
                      prefix-icon="el-icon-picture"
                      v-model="form.tele"></el-input>
          </el-form-item>
          <el-form-item label="" prop="email">
            <el-input type="text" placeholder="" size="small"
                      prefix-icon="el-icon-picture"
                      v-model="form.email"></el-input>
          </el-form-item>
          <el-form-item label="" prop="zycc">
            <el-select v-model="form.zycc" placeholder="Select" size="small" @change="Change">
              <el-option
                  v-for="item in options"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value"
              ></el-option>
            </el-select>
          </el-form-item>

          <el-form-item label="" prop="zymc">
            <el-select v-model="form.zymc" placeholder="Select" size="small">
              <el-option
                  v-for="item in options_zymc"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value"
              ></el-option>
            </el-select>
          </el-form-item>

          <el-form-item label="" prop="kcdm">
            <el-checkbox-group v-model="form.kcdm">
              <el-checkbox  v-for="item in kcdm"
                            :label="item.name+'('+item.code+')'"
                            :key="item.code"
              >
              </el-checkbox>
            </el-checkbox-group>
          </el-form-item>
          <el-tag style="color:red"></el-tag>
          <el-form-item label="">

            <el-upload
                ref="upload"
                list-type="text"
                :on-preview="handlePictureCardPreview"
                :before-upload="beforeUpload"
                :on-remove="handleRemove"
                :auto-upload="false"
                :multiple="true"
                :limit="3"
                show-file-list="true"
                :on-exceed="handleExceed"

            >
              <el-button type="primary"></el-button>
              <!--<el-button type="primary"></el-button>-->
              <template #tip>
                <div class="el-upload__tip">
                  JPG/PNG/PDF500KB
                </div>
              </template>
            </el-upload>
          </el-form-item>
        </el-form>
      <template #footer>
      <span class="dialog-footer">
        <el-button @click="dialogFormVisible = false"></el-button>
        <el-button type="primary" @click="modifySign"></el-button>
      </span>
        </template>
      </el-dialog>



      <div v-show="steps==3" style="margin-left: 30%;margin-top: 100px">
        <el-card style="width: 300px;">
          <h1 style="text-align: center"></h1>
          &nbsp;
          <span v-show="result==1">
            
          </span>
          <span v-show="result==-1">
            
          </span>
        </el-card>

      </div>

    </template>

  </div>
</template>

<script>
export default {
  //
  name: "RegisterStatus",
  data(){
    return {
      options_zymc: [],
      //
      kcdm:[],
      //
      steps:"3",
      //1-1
      result:"1",
      //
      form:{},
      //
      infoData:{
          name: "",
          sex: "",
          sfzh: "",
          ticket: "",
          tele: "",
          email: "",
          zycc: "",
          zymc: "",
          kcdm: "",
          file: ""
      },
    }
  },
  mounted:function(){
    //
    this.JudgeRes();//
  },
  methods:{
    //  file
    segmenteFile(file){
      let array = file.split('|');
      //console.log(array)
      //console.log(array.length)
      let list = [];
      for(let i = 0 ; i < array.length ; i++ ){
        let names = array[i].split('_');
        list.push({'fname':names[1],'fsrc': this.$store.state.baseurl +array[i]})
      }
      //console.log(list)
      return list;
    },
    //
    JudgeRes(){
      let param = new URLSearchParams();
      param.append("username",this.$store.state.user.username )
      this.axios.post("signupres",
          param
      ).then((response) => {
        console.log(response.data)
        console.log(response.data['msg'])
        this.$forceUpdate()         //
        if( response.data['msg'] == "ok"){
          //  res01-12
          if( response.data['res'] == "2" ){      //
            this.steps = "1";
          }else if( response.data['res'] == "0"  ){     //
            this.steps = "2";
            let param = new URLSearchParams();
            param.append("username",this.$store.state.user.username)
            this.axios.post("signupinfo",
                param
            ).then((response) => {
              this.infoData.name = response.data['name']
              this.infoData.sex = response.data['sex']
              this.infoData.ticket = response.data['ticket']
              this.infoData.tele = response.data['tele']
              this.infoData.email = response.data['email']
              this.infoData.zycc = response.data['zycc']
              this.infoData.zymc = response.data['zymc']
              this.infoData.kcdm = response.data['kcdm']
              this.infoData.file = response.data['file']
            })
          }else if( response.data['res'] == "1" || response.data['res'] == "-1"  ){     //
            this.steps = "3";
            this.result = response.data['res'];
          }

        }
      })
    },
    //
    goSignUp(){
      //this.$router.push("/success/signupinfo");
      this.$forceUpdate()         //
      this.getCode();             //
      this.dialogFormVisible = true;
      this.form  = this.infoData
      //
      //1.
      if( this.infoData.sex == "" ){
        this.form.sex = "1"
      }else{
        this.form.sex = "2"
      }
      //2.
      this.form.kcdm = []
      // 
      let array = this.infoData.kcdm.split(",")
      for(let i = 0 ; i < array.length ; i++ ){
        this.form.kcdm.push(array[i])
      }

    },
    Change(){
      this.GetCode();
      this.form.zymc = ""
      if( this.form.zycc == "" ){
        this.options_zymc = [
          {
            value: '',
            label: '',
          },
          {
            value: '',
            label: '',
          },
        ]
      }else if( this.form.zycc == "" ) {
        this.options_zymc = [
          {
            value: '',
            label: '',
          },
          { value: '',
            label: '',
          },
        ]
      }
    },
    //
    modifySign(){

    },
    //
    getCode(){
      console.log("" )
      this.axios.post("showcode"
      ).then((response) => {
        console.log(response.data)
        if (response.data['msg'] == "ok") {
          this.kcdm = response.data["course"]
        }
      })
    },

    handleRemove(file, fileList) {
      console.log(file, fileList);
    },
    handlePictureCardPreview(file) {
      this.dialogImageUrl = file.url;
      this.dialogVisible = true;
    },
    beforeUpload(file) {
      this.param = new FormData();
      this.fileList.push(file);
      let images = [...this.fileList];
      console.log("images", images);

      //this.param.append("file1", images);

      images.forEach((img) => {
        this.param.append("file", img); // 
      });
      return false;
    },


  }
}
</script>

<style scoped>

</style>