<template>
  <div>
    <h1></h1>
    <el-table :data="codeData" style="width: 100%">
      <el-table-column prop="name" label="" width="180"  />
      <el-table-column prop="code" label="" width="180" />
      <el-table-column prop="operation" label="" width="180">
        <template  #default="scope">
          <el-button type="primary" circle @click="editCode(scope.$index)" >
            <i class="el-icon-edit"></i>
          </el-button>
          <el-button type="danger" icon="Delete" circle @click="deleteCode(scope.$index)">
            <i class="el-icon-delete"></i>
          </el-button>
        </template>
      </el-table-column>

    </el-table>

    <el-divider></el-divider>
    <h1></h1>
    <el-form :model="param" :rules="rules" ref="addCode" label-width="80px">

      <el-form-item label="" prop="code">
        <el-input type="text" placeholder="" size="small"
                  prefix-icon="el-icon-info"
                  v-model="param.code" style="width: 200px;"></el-input>
      </el-form-item>
      <el-form-item label="" prop="name">
        <el-input type="text" placeholder="" size="small"
                  prefix-icon="el-icon-info"
                  v-model="param.name" style="width: 200px;"></el-input>
      </el-form-item>
      &nbsp;&nbsp;&nbsp;<el-button type="danger" @click="Submit"></el-button>
    </el-form>

    <el-dialog :visible="dialogFormVisible" title="" :close-on-click-modal="false" :append-to-body="true">
      <el-form :model="form">
        <el-form-item label="" label-width="140px">
          <el-input v-model="form.code" autocomplete="off" />
        </el-form-item>
        <el-form-item label="" label-width="140px">
          <el-input v-model="form.name" autocomplete="off" />
        </el-form-item>

      </el-form>
      <template #footer>
      <span class="dialog-footer">
        <el-button @click="dialogFormVisible = false"></el-button>
        <el-button type="primary" @click="modifyCode"></el-button>
      </span>
      </template>
    </el-dialog>

  </div>
</template>

<script>
import ElementUI from "element-ui";

export default {
  //
  name: "AdminManage",
  data(){
    return {
      //
      dialogFormVisible:false,
      param:{
        code:"",
        name:""
      },
      // 
      form:{
        code:"",
        name:""
      },
      index:-1,
      //
      codeData:[ ],
      //
      rules: {
        code: [
          {required: true, message: "", trigger: "blur"}
        ],
        name: [
          {required: true, message: "", trigger: "blur"}
        ],
      }
    }
  },
  mounted(){
      this.$forceUpdate()         //
      this.GetCode();
  },
  methods:{
    //
    GetCode(){
      //console.log("" )
      this.axios.post("showcode"
      ).then((response) => {
        console.log(response.data)
        if (response.data['msg'] == "ok") {
          this.codeData = response.data["course"]
        }
      })
    },
    //
    modifyCode(){
      this.dialogFormVisible = false;
      let index = this.index
      if( this.codeData[index]['code'] ==  this.form.code && this.codeData[index]['name'] ==  this.form.name ){
        //
        this.$message({
          type: 'error',
          message: ''
        });
        return;
      }
      console.log(index+" "+this.codeData[index]['code'])
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        let params = new URLSearchParams();
        params.append('oldcode',this.codeData[index]['code']);
        params.append('oldname',this.codeData[index]['name']);
        params.append('newcode',this.form.code);
        params.append('newname',this.form.name);

        this.axios.post("modifycode",
            params
        ).then((response) => {
          console.log(response.data)
          if (response.data['msg'] == "ok") {
            this.$forceUpdate()         //
            this.GetCode();             //
          }
        })
        this.$message({
          type: 'success',
          message: '!'
        });
        this.GetCode();
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        });
      });


    },
    //
    editCode(index){
      this.index = index
      this.$forceUpdate()         //
      this.dialogFormVisible = true;
      this.form.code = this.codeData[index]['code']
      this.form.name = this.codeData[index]['name']

    },
    //
    deleteCode(index){
      console.log(index+" "+this.codeData[index]['code'])
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        let params = new URLSearchParams();
        params.append('code',this.codeData[index]['code']);
        params.append('name',this.codeData[index]['name']);
        this.axios.post("deletecode",
            params
        ).then((response) => {
          console.log(response.data)
          if (response.data['msg'] == "ok") {
            this.codeData = response.data["course"]
            this.$forceUpdate()         //
          }
        })
        this.$message({
          type: 'success',
          message: '!'
        });
        this.GetCode();
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        });
      });

    },
    Submit(){
      this.$confirm(''+this.param.code+""+this.param.name+"", '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        //axiosnull
        let params = new URLSearchParams();
        params.append('code', this.param.code);
        params.append('name', this.param.name);
        console.log(params)
        this.axios.post("addcode",
            params
        ).then((response) => {
          console.log(response.data)
          console.log(response.data['msg'])
          if( response.data['msg'] == "ok"){
            ElementUI.Message({
              message: '',
              type: 'success'
            });
            this.GetCode();
          }else {
            ElementUI.Message({
              message: '',
              type: 'error'
            });
          }
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        });
      });


    },


  }

}
</script>

<style scoped>

</style>